import hiatusLogo from '../../static/backgrounds/hiatus/logo.png'
import backLogo from '../../static/backgrounds/back/logo.svg'
import HeaderItem from '../HeaderItem'
import "./headerStyle.css"
import { Link } from 'react-router-dom'

const Header = ({ isPublished }: Props) => {
	return (
		<div id="header">
			<Link to="/"><img id="logo" alt="Hunter x Hunter logo" className={isPublished ? "publishedLogo":  "hiatusLogo"}  src={isPublished ? backLogo: hiatusLogo}></img></Link>
			<div id="items">
					<HeaderItem className={isPublished ? "ItemPublished":  "ItemHiatus"} to="/hiatusTable" text="Hiatus Table" />
					<HeaderItem className={isPublished ? "ItemPublished":  "ItemHiatus"} to="/authorComments" text="Author Comments" />
					<HeaderItem className={isPublished ? "ItemPublished":  "ItemHiatus"} to="/about" text="About" />
			</div>

		</div>
	)
}

interface Props {
	isPublished: Boolean | undefined
}

export default Header