import './style.css'
import {Link} from 'react-router-dom'
const HeaderItem = ({ text, className, to,}: Props) => {
	return (<Link id="HeaderItem" className={className} to={to} ><p>{text}</p></Link>
	)
}


interface Props {
	text: string,
	className: string,
	to: string,
}

export default HeaderItem