import seriesStatusInterface from "../../helpers/interfaces/seriesStatusInterface"
import { zoomIn, fadeIn } from 'react-animations'
import './style.css'
import Radium from 'radium';
import gon from '../../static/images/characters/gon.png'
const HomeBody = ({ status }: Props) => {
	const images = [gon]
	const randomNumber = Math.floor((Math.random() * images.length))
	const styles: any = {
		zoomIn: {
			animation: 'x 1s',
			animationName: Radium.keyframes(zoomIn, 'zoomIn')
		},
		fadeIn: {
			animation: 'x 3s',
			animationName: Radium.keyframes(fadeIn, 'fadeIn'),
		}

	}
	if (!status?.isPublished) {
		return (
			<div id="homeBody">
				<div>
					<Radium.StyleRoot style={styles.fadeIn}>
						<p className="home-text">THE GREAT HIATUS ARC <br />
							CONTINUES AS OF ISSUE {status?.issue}</p>
					</Radium.StyleRoot>
					<Radium.StyleRoot style={styles.zoomIn}>
						<hr id="line" />
					</Radium.StyleRoot>
					<Radium.StyleRoot style={styles.fadeIn}>
						<p className="home-text">While you are at it, check out more<br />cool stuff about the manga!</p>
					</Radium.StyleRoot>
				</div>
				<img alt="Gon is questioning his life decisions" src={images[randomNumber]}></img>
			</div>
		)
	}
	else {
		return (
			<div id="homeBody">
				<div>
					<Radium.StyleRoot style={styles.fadeIn}>
						<p className="home-text">THE MANGA IS FINALLY BACK ACCORDING TO ISSUE {status?.issue}!!</p>
					</Radium.StyleRoot>
					<Radium.StyleRoot style={styles.zoomIn}>
						<hr id="line" />
					</Radium.StyleRoot>
					<Radium.StyleRoot style={styles.fadeIn}>
						<p className="home-text">While you are at it, check out more<br />cool stuff about the manga!</p>
					</Radium.StyleRoot>
				</div>
				<img alt="Gon is questioning his life decisions" src={images[randomNumber]}></img>
			</div>
		)
	}
}

interface Props {
	status?: seriesStatusInterface
}

export default HomeBody