import './style.css'
import seriesStatusInterface from '../../helpers/interfaces/seriesStatusInterface'
import SerializationYear from '../../helpers/interfaces/SerializationYear'
import {memo} from 'react'
const HiatusTable: React.FC<Props> = ({status, issues}) => {
	return <div id="hiatusTable">
		<div id="table">
			<h1>Hiatus Table</h1>
			{issues?.map((calenderYear, i) => {
				return <div id="row" key={i}>
					<div id="year">{calenderYear.year}</div>
					<div id="publishingYear">
						{calenderYear.issues.map((issue, j) => {
							return <div title={`Issue ${issue.issue}\n${issue.chapter === -1 ? "On hiatus" : "Chapter " + issue.chapter}`} key={j} className="issue" id={issue.isPublished ? "active" : "absent"}>
							<p id="chapterNumber">{issue.chapter === -1 ? "": issue.chapter}</p></div>
						})}
					</div>
				</div>
			})}
		</div>
	</div>

}
interface Props {
	status: seriesStatusInterface | undefined,
	issues: SerializationYear[] | undefined
}
export default memo(HiatusTable)