import { useState } from 'react';
import { Route, BrowserRouter as Router } from 'react-router-dom';
import './App.css';
import HomeBody from './components/HomeBody';
import HiatusTable from './components/HiatusTable';
import Home from './components/Home/index'
import Loading from './components/Loading';
import AnimatedSwitch from './components/Router/AnimatedSwitch';
import seriesStatusInterface from './helpers/interfaces/seriesStatusInterface';
import useAPI from './hooks/useAPI'
import SerializationYear from './helpers/interfaces/SerializationYear';
import About from './components/About';
import AuthorComments from './components/AuthorComments';

function App() {
	const [status, setStatus] = useState<seriesStatusInterface>()
	const [issues, setIssues] = useState<SerializationYear[]>()
	const [loading, setLoading] = useState<Boolean>(true)
	useAPI(setStatus, setIssues, setLoading)
	if (loading) {
		return <Loading />
	}

	return (<div className="App">
		<Router>
			<AnimatedSwitch>
				<Route exact path="/">
					<Home className="Homepage" status={status}>
						<HomeBody status={status} />
					</Home>
				</Route>
				<Route exact path="/hiatusTable">
					<Home className="hiatusTable" status={status}>
						<HiatusTable issues={issues} status={status} />
					</Home>
				</Route>
				<Route exact path="/authorComments">
					<Home className="HomePage" status={status}>
						<AuthorComments />
					</Home>
				</Route>
				<Route exact path="/about">
					<Home className="hiatusTable" status={status}>
						<About />
					</Home>
				</Route>
			</AnimatedSwitch>
		</Router>
	</div>)

}

export default App;
