import {useEffect } from "react"
import seriesStatusInterface from "../../helpers/interfaces/seriesStatusInterface"
import Header from "../Header"
import './homeStyle.css'
import '../../backgroundStyles/hiatusBackgroundStyle.css'

const Home: React.FC<Props>= ({status, children, className})=> {
	useEffect(() => {
		if(status?.isPublished)
			document.body.classList.add('BodyPublished')
		else{
			document.body.classList.add('BodyHiatus')
		}
	})
	return (
		<div className={!status?.isPublished ? `Home HomeHiatus ${className}`: `Home HomePublished ${className}`}>
			<Header isPublished={status?.isPublished} />
			{children}
		</div>
	)
}

interface Props {
	status: seriesStatusInterface | undefined,
	// classname is used for define the sizes of the page in grid-template-row
	className: string
}


export default Home