import { useEffect } from "react";
import SerializationYear from "../helpers/interfaces/SerializationYear";
import seriesStatusInterface from "../helpers/interfaces/seriesStatusInterface";
import { getJumpIssues, getSeriesStatus } from "../services/api";
function useAPI(setStatus: React.Dispatch<React.SetStateAction<seriesStatusInterface | undefined>>, setIssues: React.Dispatch<React.SetStateAction<SerializationYear[] | undefined>>, setLoading: React.Dispatch<React.SetStateAction<Boolean>>){
	useEffect(() => {
		async function fetchStatus(){
			setLoading(true)
			setTimeout(async () => {
			setStatus(await getSeriesStatus())
			setIssues(await getJumpIssues())
			setLoading(false)

			}, 1500)
		}
		fetchStatus()
	}, [setStatus,setIssues, setLoading])
}

export default useAPI