import axios from "axios"
import SerializationYear from "../helpers/interfaces/SerializationYear"
import seriesStatusInterface from "../helpers/interfaces/seriesStatusInterface"

const getSeriesStatus = async (): Promise<seriesStatusInterface> => {
	return (await axios.get<seriesStatusInterface>('/status')).data
}

const getJumpIssues = async (): Promise<SerializationYear[]> => {
	return (await axios.get<SerializationYear[]>('/jumpIssues')).data
}


export {
	getSeriesStatus,
	getJumpIssues
}